.testimonials {
    padding: 0 10vw 12rem;
    background-color: rgb(247, 247, 247);

    @include atSmall {
        padding: 0rem 20vw 12rem;
    }

    & h1 {
        padding: 6rem 0;
        // padding-bottom: 5vh;
        text-align: center;
        font-size: 4rem;

            @include atSmall {
                font-size: 7rem;
                padding: 7rem 0;
            }
    }

    &-card-container {
        border: 1px solid rgb(168, 168, 168); 
        padding: 2rem 0;
        margin: 0 1vw;
        border-radius: 3%;
    }

    &-card-header {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        grid-template-rows: repeat(2, 1fr);
    }

    &-card-img {
        grid-column: 1 / 3;
        grid-row: 1 / 3;
        height: 60px;
        width: 60px;
    }

    &-card-author {
        color: red;
        grid-column: 1 / 3;
        grid-row: 3 / 4;
    }

    &-card-description {
        font-size: 1.5rem;
    }
}



