.portfolio {
    padding: 0;

    &-header {
        text-align: center;
        padding: 7rem 0;
        background-color: #dadada;
        font-size: 5rem;
        word-wrap: break-word;

        @include atSmall {
            font-size: 7rem;
        }
    }

/************************************ PORTFOLIO CARD TEST ***********************************/

    &-box {
        background-color: #dadada; 
    }

    &-card {
        background-color:rgb(36, 36, 36);  
        padding: 8rem 0;

        @include atMedium  {
            display: grid;
            grid-template-columns: repeat(4, 1fr);
            grid-template-rows: 60px 1fr;
            padding: 5rem 5rem;
        }
    }

    &-title {
        grid-column: 1 / 4;
        font-size: 50px;
        text-align: left;
        padding: 0 8rem 0 3rem;
        font-family: $paintFont;

        @include atMedium  {
            padding: 0;
        }
    }

    &-desc {
        grid-column: 1 / 2;
        grid-row: 2 / 3;
        padding: 0 3rem 3rem;
        margin-right: 7rem;

        @include atMedium  {
            margin-right: 0;
            padding: 0;
            width: 375px; 
        }
    }

    &-btn {
        display: block;
        
        &-margin {
            margin: 0 0.5rem 0.5rem 0;
        }

        @include atSmaller {
            display: flex;
        }
    }

    &-img-box {
        display: flex; 
        padding: 0 3rem;

        @include atMedium  {
            grid-column: 2 / 4;
            grid-row: 2 / 3;
            padding-top: 2rem;
            text-align: center;
        }
    }

/************************************ PORTFOLIO CARD RIGHT SIDE ***********************************/

    &-box-right {
        background-color: $primary;
    }

    &-card-right {
        background-color:rgb(36, 36, 36); 
        padding: 8rem 0;

        @include atMedium  {
            display: grid;
            grid-template-columns: repeat(4, 1fr);
            grid-template-rows: 60px 1fr;
            padding: 5rem 5rem;
        }
    }

    &-title-right {
        grid-column: 3 / 5;
        text-align: right;
        font-size: 50px;
        padding: 0 3rem 0 5rem;
        font-family: $paintFont;
    }

    &-desc-right {
        grid-column: 4 / 5;
        grid-row: 2 / 3;
        text-align: right;
        padding: 0 3rem 3rem;
        margin-left: 7rem;

        @include atMedium  {
            margin-left: 0;
            padding: 0;
            width: 375px;
        }
    }

    &-btn-right {
        display: block;
        
        @include atSmaller {
            display: flex;
            justify-content: right; 
        }
    }

    &-img-box-right {
        display: flex; 
        padding: 0 3rem;
        justify-content: right;

        @include atMedium  {
            grid-column: 2 / 4;
            grid-row: 2 / 3;
            text-align: right;
            padding-top: 2rem; 
        }
    }
}

/************************************ POLYGON CLIP PATH SECTION ***********************************/

/* LEFT HAND SIDE PORTFOLIO CARD */

.custom-1 {
    clip-path: polygon(0 0, 88% 0, 94% 100%, 0% 100%);
}

@include atMedium  {
    .custom-1 {
        clip-path: polygon(0 0, 67% 0, 89% 100%, 0% 100%);
    }
}

/* RIGHT HAND SIDE PORTFOLIO CARD */

.custom-2 {
    clip-path: polygon(12% 0, 100% 0, 100% 100%, 4% 100%);
}

@include atMedium  {
    .custom-2 {
        clip-path: polygon(33% 0, 100% 0, 100% 100%, 9% 100%);
    }
}

/**************************** PORTFOLIO HOVER IMAGES ******************/

.caption-style{
    list-style-type: none;
    margin: 0px;
    padding: 0px; 
}

.caption-style li{
    float: left;
    padding: 0px;
    position: relative;
    overflow: hidden;
}

.caption-style li:hover .caption{
    opacity: 1;
}

.caption-style li:hover img{
    opacity: 1;
    transform: scale(1.15,1.15);
    -webkit-transform:scale(1.15,1.15);
    -moz-transform:scale(1.15,1.15);
    -ms-transform:scale(1.15,1.15);
    -o-transform:scale(1.15,1.15);
}

.caption-style img {
    margin: 0px;
    padding: 0px;
    float: left;
    z-index: 0;
}

.caption-style .caption{
    cursor: pointer;
    position: absolute;
    opacity: 0;
    -webkit-transition:all 0.45s ease-in-out;
    -moz-transition:all 0.45s ease-in-out;
    -o-transition:all 0.45s ease-in-out;
    -ms-transition:all 0.45s ease-in-out;
    transition:all 0.45s ease-in-out;
}

.caption-style img{
    -webkit-transition:all 0.25s ease-in-out;
    -moz-transition:all 0.25s ease-in-out;
    -o-transition:all 0.25s ease-in-out;
    -ms-transition:all 0.25s ease-in-out;
    transition:all 0.25s ease-in-out;
}

.caption-style .blur{
    background-color: rgba(255, 50, 50, 0.80);
    height: 300px;
    width: 400px;
    z-index: 5;
    position: absolute;
}

.caption-style .caption-text h1{
    text-transform: uppercase;
    font-size: 24px;
    padding-top: 2rem;
    font-weight: bold;
}

.caption-style .caption-text{
    z-index: 10;
    color: #fff;
    position: absolute;
    width: 400px;
    height: 300px;
    text-align: center;
    top:100px;
}