.nav-bar {
    background-color: $primary;
    box-shadow: 0 2px 2px $menuBoxShadow;
    height: 8.1rem;
    

    @include atMedium {
        display: flex;
        justify-content: space-between;
        transition: all .3s ease;
        grid-column: 1 / 5;
        background-color: transparent;
        box-shadow: none;
    }

    &__main-menu {
        display: none;

        &--is-visible {
            display: block;
            opacity: 1; 
            background-color: $primary;
            padding: 0 4vw 0 4vw;
            box-shadow: 0 2px 2px $menuBoxShadow;
        }

        @include atMedium {
            display: flex;
        }
    }

    & .menu {
        @include atMedium {
            display: flex;
            align-items: center;
        }  
    }

    & li {
        border-bottom: 1px solid $black;
        
        @include atMedium {
            margin-right: 2rem;
            border-bottom: none;
        }   
    }

    & li:last-of-type {
        border-bottom: none;

        @include atMedium {
            margin-right: 0;
        } 
    }

    & a {
        color: $white;
        font-size: 1.5rem;
        font-family: $mainFont;
        
        @include atMedium {
            padding: .5rem 1rem;
        }

        &:hover {
            color: $black;

            @include atMedium {
                color: $primary;
            }
        }
    } 
}

// FIXED NAV BAR LINK HIGHLIGHT ON SECTION

.mPS2id-highlight-first {
    color: black !important;
}
